import React, { Component } from "react";
import * as axios from "axios";

// Template components
import { LinksBlock } from "./template/LinksBlock";
import { Text } from "./template/Text";
import { ImageBlock } from "./template/ImageBlock";
import { CodeBlock } from "./template/CodeBlock";
import { Title } from "./template/Title";

export class Page extends Component {
  getPageItems = () => {
    if (this.props.root.currentPage.length === 0) {
      axios.get(this.props.root.api.pages_api.page)
        .then((response) => {
          this.props.root.actionCreator("SET_CURRENT_PAGE", response.data[0]);
        });
    }
  }

  createPageItem = (item) => {
    switch(item.name) {
      case "title": {
        return <Title value={item.value} />
      }

      case "imageBlock": {
        return <ImageBlock
              image={item.items.image}
              id={item.items.id}
              sublinks={[ ...item.items.sublinks]}
            />
      }

      case "codeBox": {
        return <CodeBlock 
          lines={[ ...item.items.codeLines]} 
          result={[...item.items.resultLines]} 
          highlight={this.props.root.syntaxHighlight.highlight}
        />
      }

      case "text": {
        if (Object.keys(this.props.root.texts).length === 0) {
          axios.get(item.source)
            .then((response) => {
              this.props.root.actionCreator("ADD_NEW_TEXT", { name: item.id, text: response.data });
            });
        }
  
        return <Text value={this.props.root.texts[item.id]} id={item.id} />
      }

      case "links": {
        let links = item.items;
        return <LinksBlock links={[ ...links]} />
      }

      default: {
        return {};
      }
    }
  }

  createPage = (pageItems) => {
    let page = pageItems.map((item) => {
      return this.createPageItem(item);
    });

    return page;
  }

  createPagination = (pageId, nextPage) => {
    let previous, next;
    let pageLinks = [];

    if (pageId < 2) {
      previous = <div></div>;
    } else {
      previous = <div className="tp-pagination_button previous-button" onClick={() => {this.setPage(pageId - 1)}}>
        <a href="#"><span>Previous</span></a>
      </div>;
    }

    if (!nextPage) {
      next = <div></div>;
    } else {
      next = <div className="tp-pagination_button next-button" onClick={() => {this.setPage(pageId + 1)}}>
        <a href="#"><span>Next</span></a>
      </div>;
    }

    return (
      <div className="tp-pagination_block">
        {previous}
        {next}
        <div className="tp-page-searcher">
          <input 
            type="text" 
            id="pageSearcher" 
            placeholder="Page number" 
            value={this.props.root.pageSearcherValue} 
            onChange={ (e) => this.setPageSearcherValue(e.target.value)} 
          />
          <div className="tp-page-searcher_button" onClick={() => this.setPage(this.props.root.pageSearcherValue)}>
            <a href="#"><span>Найти</span></a>
          </div>
        </div>
      </div>
    );
  }

  setPageSearcherValue = (newValue) => {
    if (newValue === "" || newValue.match(/[0-9]/) !== null) {
      this.props.root.actionCreator("SET_PAGE_SEARCHER_VALUE", newValue);
    } else {
      alert("You cannot type something but number!");
    }
  }

  setPage = (pageId) => {
    axios.get(`https://sha1om.github.io/dooboox_api/pages/page-${pageId}`)
      .then((response) => {
        if (response.data) {
          this.props.root.actionCreator("SET_CURRENT_PAGE", response.data[0]);
        }
      });
  }
}
